import VueRouter from 'vue-router'

var router = new VueRouter({
    routes: [
        {
            path: '/',
            redirect: '/home'
        },
        {
            path: '/home',
            meta: {
                index: 1,
                title: '首页',
            },
            component: resolve => require(['./page/home.vue'], resolve)
        },
        {
            path: '/waterCost',
            meta: {
                index: 2,
                title: '水费',
            },
            component: resolve => require(['./page/waterCost.vue'], resolve)
        },
        {
            path: '/powerCost',
            meta: {
                index: 3,
                title: '电费',
            },
            component: resolve => require(['./page/powerCost.vue'], resolve)
        },
        {
            path: '/gasCost',
            meta: {
                index: 4,
                title: '燃气费',
            },
            component: resolve => require(['./page/gasCost.vue'], resolve)
        },
        {
            path: '/rentCost',
            meta: {
                index: 5,
                title: '房租',
            },
            component: resolve => require(['./page/rentCost.vue'], resolve)
        },
        {
            path: '/carbonNeutral',
            meta: {
                index: 6,
                title: '碳中和',
            },
            component: resolve => require(['./page/carbonNeutral.vue'], resolve)
        },
        {
            path: '/finance',
            meta: {
                index: 7,
                title: '财务指标',
            },
            component: resolve => require(['./page/finance.vue'], resolve)
        },
        {
            path: '/monitoring',
            meta: {
                index: 8,
                title: '监控中心',
            },
            component: resolve => require(['./page/monitoring.vue'], resolve)
        },
    ]
})

export default router;